import React from "react";
import { Form, Input, Button, Select } from "antd";
import { useHistory } from "react-router-dom";
import Header from '../../../components/Header'
import HeadingSubs from '../../../components/HeadingSubs'
import Spacer from '../../../components/Spacer'

const { Option } = Select;

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 24 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 24 },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
  },
};

const Forms = () => {
  const [form] = Form.useForm();
  const history = useHistory();
  
  const handleSignup = (values: any) => {
    history.push('thankyou?topic=borrower');
  };

  return (
    <>
      <Header onBackLink='register' />
      <HeadingSubs 
        title='Mendaftar sebagai borrower'
        subtitle='Lorem ipsum dolor sit amet, has facer noluisse in. Ut cum commodo virtute. Vix no omnis iudicabit sadipscing.'
        alignment='left'
      />
      <Form
        {...formItemLayout}
        form={form}
        layout={'vertical'}
        name="register"
        onFinish={handleSignup}
        scrollToFirstError
      >
        <Form.Item
          name="name"
          label="Nama Lengkap"
          rules={[
            {
              required: true,
              message: "Mohon masukkan nama lengkap Anda",
            },
          ]}
        >
          <Input placeholder="Ketik nama lengkap Anda" />
        </Form.Item>

        <Form.Item
          name="phone"
          label="Nomor Telepon"
          rules={[
            {
              required: true,
              message: "Mohon masukkan nomor handphone Anda",
            },
          ]}
        >
          <Input placeholder="Ketik nomor handphone Anda" addonBefore="+62" />
        </Form.Item>

        <Form.Item
          name="email"
          label="Alamat Email"
          rules={[
            {
              type: "email",
              message: "Alamat email tidak valid",
            },
            {
              required: true,
              message: "Mohon masukkan email yang terdaftar",
            },
          ]}
        >
          <Input placeholder="Ketik alamat email Anda" />
        </Form.Item>
        
        <Form.Item
          name="cityy"
          label="Kota Domisili"
          rules={[
            {
              required: true,
              message: "Mohon mencari kota domisili Anda",
            },
          ]}
        >
          <Select
            showSearch
            placeholder="Ketik alamat email Anda" 
            optionFilterProp="children"
          >
            <Option value="Jakarta">Jakarta</Option>
            <Option value="Bogor">Bogor</Option>
            <Option value="Depok">Depok</Option>
            <Option value="Tangerang">Tangerang</Option>
            <Option value="Bekasi">Bekasi</Option>
          </Select>
        </Form.Item>

        <Spacer setHeight='40px' />

        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit" block>
            Daftarkan
          </Button>
        </Form.Item>

      </Form>
    </>
  );
};

export default Forms;
