import { Col, Row } from "antd";
import React from "react";
import { Helmet } from "react-helmet-async";
import Background from "./components/Background";
import Choices from "./components/Choices";
import LenderForms from "./components/LenderForms";
import BorrowerForms from "./components/BorrowerForms";
import getQueryString from "../../helpers/getQueryString";

interface Location {
  search: string;
  pathname: string;
}

type Props = {
  location: Location;
};

const Register: React.FC<Props> = ({ location }) => {
  const user = getQueryString(location?.search, "user");
  let content = <Choices />
  switch(user) {
    case 'lender': 
      content = <LenderForms />;
    break;
    case 'borrower': 
      content = <BorrowerForms />;
    break;
    default: 
      content = <Choices />;
  }

  return (
    <>
      <Helmet>
        <title>Mekar.id - Register</title>
      </Helmet>
      <Row>
        <Col xs={0} sm={0} lg={12}>
          <Background />
        </Col>
        <Col xs={24} sm={24} lg={12}>
          <div style={{
            margin: "24px 16px",
            padding: 24,
          }}>
            {content}
          </div>
        </Col>
      </Row>
    </>
  );
};

export default Register;
