import React from "react";
import { Row, Col } from "antd";

type Props = {
  title: string;
  subtitle: string;
  alignment: string;
};

const HeadingSubs: React.FC<Props> = ({ title, subtitle, alignment }) => {
  return (
    <>
      <Row>
        <Col xs={24}>
          <h2>{ title }</h2>
          {
            alignment === 'center' ? 
            <p style={{ color: '#9DA8B7', textAlign: 'center' }}>{subtitle}</p> : 
            <p style={{ color: '#9DA8B7', textAlign: 'justify' }}>{subtitle}</p>
          }
        </Col>
      </Row>
    </>
  );
};

export default HeadingSubs;
