import React, { useState } from "react";
import { Form, Input, Button, Row, Col, Checkbox } from "antd";
import { Link, useHistory } from "react-router-dom";
import Header from "../../../components/Header";
import HeadingSubs from "../../../components/HeadingSubs";
import Spacer from "../../../components/Spacer";

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 24 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 24 },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
  },
};

const Forms = () => {
  const [form] = Form.useForm();
  const history = useHistory();
  const [buttonDisabled, setButtonDisabled] = useState(true);

  const handleCheckTerms = (check: any) => {
    setButtonDisabled(!check.target.checked);
  };

  const handleSignup = (values: any) => {
    history.push('thankyou?topic=lender');
  };

  return (
    <>
      <Header onBackLink="register" />
      <HeadingSubs
        title="Buat akun baru sebagai lender"
        subtitle="Buat akun Anda untuk melihat daftar pinjaman dan dapatkan imbal hasil dari pendanaan Anda"
        alignment="left"
      />
      <Form
        {...formItemLayout}
        form={form}
        layout={"vertical"}
        name="register"
        onFinish={handleSignup}
        onFinishFailed={() => {}}
        scrollToFirstError
      >
        <Form.Item
          name="name"
          label="Nama Lengkap"
          rules={[
            {
              required: true,
              message: "Mohon masukkan nama lengkap Anda",
            },
          ]}
        >
          <Input placeholder="Ketik nama lengkap Anda" />
        </Form.Item>

        <Form.Item
          name="phone_number"
          label="Nomor Telepon"
          rules={[
            {
              required: true,
              message: "Mohon masukkan nomor handphone Anda",
            },
          ]}
        >
          <Input placeholder="Ketik nomor handphone Anda" addonBefore="+62" />
        </Form.Item>

        <Form.Item
          name="email"
          label="Alamat Email"
          rules={[
            {
              type: "email",
              message: "Alamat email tidak valid",
            },
            {
              required: true,
              message: "Mohon masukkan email yang ingin didaftarkan",
            },
          ]}
        >
          <Input placeholder="Ketik alamat email Anda" />
        </Form.Item>

        <Form.Item
          name="password"
          label="Kata Sandi"
          rules={[
            {
              required: true,
              message: "Mohon masukkan kata sandi Anda",
            },
          ]}
          hasFeedback
        >
          <Input.Password placeholder="Ketik kata sandi Anda" />
        </Form.Item>

        <Form.Item
          name="password_confirm"
          label="Konfirmasi Kata Sandi"
          dependencies={["password"]}
          hasFeedback
          rules={[
            {
              required: true,
              message: "Mohon konfirmasi kata sandi Anda",
            },
            ({ getFieldValue }) => ({
              validator(rule, value) {
                if (!value || getFieldValue("password") === value) {
                  return Promise.resolve();
                }
                return Promise.reject(
                  "Konfirmasi kata sandi yang Anda masukkan tidak sesuai"
                );
              },
            }),
          ]}
        >
          <Input.Password placeholder="Ketik ulang kata sandi Anda" />
        </Form.Item>

        <Row>
          <Col>
            <Checkbox onChange={handleCheckTerms}>
              Saya telah membaca dan menyetujui{" "}
              <a href="https://policies.google.com/privacy ">
                kebijakan privasi
              </a>{" "}
              dan{" "}
              <a href="https://policies.google.com/terms">syarat ketentuan</a>
            </Checkbox>
          </Col>
        </Row>

        <Spacer setHeight="40px" />

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            block
            disabled={buttonDisabled}
          >
            Buat Akun
          </Button>
          <Spacer setHeight="20px" />
          <Button block>
            <Link to="/">Kembali ke homepage</Link>
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default Forms;
